import React, {Component} from 'react';
import StepCard from './StepCard';
import Reflect from './Reflect';
import InteractiveSVG from './InteractiveSVG';
import $ from 'jquery';

class ImageInteractivePractice extends Component {
   constructor() {
      super();
      this.state = {
      currentButton : 1, mode : 'learn', currentAnswerMark : 1, mediaDimTag : '',
      testKey : 1, audioVol : 1, userTested : false, PNUM : null, testPointsPer : 50, learnPointsPer : 100, lastTestPointsPer : null
      };
      this.childClick = this.childClick.bind(this);
      this.startOver = this.startOver.bind(this);
      this.reset = this.reset.bind(this);
      this.upOne = this.upOne.bind(this);
      this.testMode = this.testMode.bind(this);
      this.learnMode = this.learnMode.bind(this);
      this.correctAnswer = this.correctAnswer.bind(this);
      this.updateTest = this.updateTest.bind(this);
      this.setAudio = this.setAudio.bind(this);
      this.repeatStep = this.repeatStep.bind(this);
      this.userTested = this.userTested.bind(this);
      this.updateState = this.updateState.bind(this);
      this.updateMedia = this.updateMedia.bind(this);
      this.incorrectAnswer = this.incorrectAnswer.bind(this);
   }
   userTested(res) {
      if (res) this.setState({userTested : true});
   }
   updateMedia() {
     let wdt = $('#media-container').width();
     let hgt = $('#media-container').height();
     let mediatag;
     if (wdt / hgt <= 1.2) mediatag = '_square';
     else mediatag = '';
     this.setState({mediaDimTag : mediatag, PNUM : this.props.pnum, testPointsPer : this.props.clickData.lastTestPointsPer});
     if (wdt / hgt > 1.72) this.setState({mediaDims : {height : '65vh'}});
     else this.setState({mediaDims : {height : '54vh'}});
   }
   componentDidMount() {
      this.updateMedia();
      let wdt = $('#media-container').width();
      let hgt = $('#media-container').height();
      if (wdt / hgt > 1.72) this.setState({mediaDims : {height : '65vh'}});
      window.addEventListener('resize', this.updateMedia.bind(this));
      setTimeout(function(){window.MathJax.Hub.Queue(['Typeset', window.MathJax.Hub]);}, 50);
   }
   childClick() {
      if (this.state.mode === 'test') return;
      this.setState({currentButton : this.state.currentButton + 1}, function() {
      const stepWindow = document.getElementById('steps-container');
      const stepWindowLoc = document.getElementById('steps-container').offsetTop;
      const cardLoc = document.getElementById(`card${this.state.currentButton - 1}`).offsetTop;
      setTimeout(function(){stepWindow.scroll({top: cardLoc - stepWindowLoc - 2, behavior: 'smooth' });}, 800);
      });
      let sounds = document.getElementsByTagName('audio');
      for (let i = 0; i < sounds.length; i++) {sounds[i].pause(); sounds[i].currentTime = 0}
      let audio = document.getElementById(`${this.props.audio_abbrev}${this.state.currentButton}`);
      if (audio) {
         audio.src = this.props.probdata[this.state.currentButton - 1].fields.learnaudio[0].url || null;
         audio.volume = this.state.audioVol;
         setTimeout(function(){audio.play();}, 800);
      }
   }
   correctAnswer(fin) {
      this.setState({currentAnswerMark : this.state.currentAnswerMark + 1}, function() {
      const stepWindow = document.getElementById('steps-container');
      const stepWindowLoc = document.getElementById('steps-container').offsetTop;
      if (!fin) {
         const cardLoc = document.getElementById(`card${this.state.currentAnswerMark}`).offsetTop;
         setTimeout(function(){stepWindow.scroll({top: cardLoc - stepWindowLoc - 2, behavior: 'smooth' });}, 800);
      }
      else {
         setTimeout(function(){stepWindow.scroll({top: 9999, behavior: 'smooth' });}, 800);
      }
      if (this.state.currentAnswerMark > this.props.probdata.length - 2) {
      $('.choice-button').attr('disabled', true);
      }
      });
      $('.userbutton').css({'background-color' : '#ffffff'});
      var choice_colors = {'A' : 'rgba(150, 226, 252, 0.5)', 'B' : 'rgba(178, 178, 204, 0.5)',
                           'C' : 'rgba(153, 229, 153, 0.5)', 'D' : 'rgba(228, 203, 228, 0.5)'};
      var choice_map = {'A' : 0, 'B' : 1, 'C' : 2, 'D' : 3};
      let testcode, testch;
      if (this.state.testKey % this.state.PNUM === 1 || this.state.PNUM === 1) {testcode = 'test'; testch = 1;}
      else if (this.state.testKey % this.state.PNUM === 0) {testcode = `test${this.state.PNUM}`; testch = this.state.PNUM;}
      else {testcode = `test${this.state.testKey % this.state.PNUM}`; testch = this.state.testKey % this.state.PNUM;}
      let corLtr = this.props.probdata[this.state.currentAnswerMark - 2].fields[`testans${testch}`];
      $(`#${testcode}${this.state.currentAnswerMark - 2}${choice_map[corLtr]}`).css('background-color', choice_colors[corLtr]);
      if (this.state.testKey % this.state.PNUM !== 0) {
         this.setState({testPointsPer : 50, lastTestPointsPer : 50});
         let data = this.props.clickData;
         data.lastTestPointsPer = 50;
         localStorage.setItem(this.props.audio_abbrev.slice(0, this.props.audio_abbrev.indexOf('_')), JSON.stringify(data));
      }
      else if (this.state.testKey % this.state.PNUM === 0 && this.state.currentAnswerMark > this.props.probdata.length - 2) {
         let data = this.props.clickData;
         data.banked_score += data.score;
         data.score = 0;
         localStorage.setItem(this.props.audio_abbrev.slice(0, this.props.audio_abbrev.indexOf('_')), JSON.stringify(data));
         let ding = document.getElementById('quizshow');
         ding.pause();
         ding.currentTime = 0;
         let dong = document.getElementById('quizshowend');
         dong.volume = 0.25;
         dong.play();
      }
   }
   incorrectAnswer() {
      if (this.state.testKey % this.state.PNUM !== 0) {
      let data = this.props.clickData;
      data.lastTestPointsPer = Math.ceil(this.state.testPointsPer / 2);
      localStorage.setItem(this.props.audio_abbrev.slice(0, this.props.audio_abbrev.indexOf('_')), JSON.stringify(data));
      this.setState({testPointsPer : Math.ceil(this.state.testPointsPer / 2), lastTestPointsPer : Math.ceil(this.state.testPointsPer / 2)});
      }
      else {
      let data = this.props.clickData;
      data.score = data.score / 2;
      localStorage.setItem(this.props.audio_abbrev.slice(0, this.props.audio_abbrev.indexOf('_')), JSON.stringify(data));
      }
   }
   startOver() {
      this.setState({currentButton : 2}, function() {
         let sounds = document.getElementsByTagName('audio');
         for (let i = 0; i < sounds.length; i++) {sounds[i].pause(); sounds[i].currentTime = 0}
         let audio = document.getElementById(`${this.props.audio_abbrev}${this.state.currentButton - 1}`);
         if (audio) {
         audio.src = this.props.probdata[this.state.currentButton - 2].fields.learnaudio[0].url || null;
         audio.volume = this.state.audioVol;
         audio.play();
         }
         }
      );
      const stepWindow = document.getElementById('steps-container');
      stepWindow.scroll({top: 0, behavior: 'smooth' });
      $('#reflectQuestion').val('');
   }
   reset() {
      this.setState({currentButton : 1}, function() {
         let sounds = document.getElementsByTagName('audio');
         for (let i = 0; i < sounds.length; i++) {sounds[i].pause(); sounds[i].currentTime = 0}
         }
      );
      const stepWindow = document.getElementById('steps-container');
      stepWindow.scroll({top: 0, behavior: 'smooth' });
      $('#reflectQuestion').val('');
   }
   upOne(evt) {
      let sounds = document.getElementsByTagName('audio');
      for (let i = 0; i < sounds.length; i++) {sounds[i].pause(); sounds[i].currentTime = 0}
      let audio = document.getElementById(`${this.props.audio_abbrev}${this.state.currentButton - 2}`);
      if (audio) {
         audio.src = this.props.probdata[this.state.currentButton - 3].fields.learnaudio[0].url || null;
         audio.volume = this.state.audioVol;
         audio.play();
      }
      evt.stopPropagation();
      this.setState({currentButton : this.state.currentButton - 1}, function() {
      if (this.state.currentButton > 1) {
      const stepWindow = document.getElementById('steps-container');
      const stepWindowLoc = document.getElementById('steps-container').offsetTop;
      const cardLoc = document.getElementById(`card${this.state.currentButton - 1}`).offsetTop;
      setTimeout(function(){stepWindow.scroll({top: cardLoc - stepWindowLoc, behavior: 'smooth' });}, 100);
      }
      });
   }
   repeatStep(evt) {
      let sounds = document.getElementsByTagName('audio');
      for (let i = 0; i < sounds.length; i++) {sounds[i].pause(); sounds[i].currentTime = 0}
      let audio = document.getElementById(`${this.props.audio_abbrev}${this.state.currentButton - 1}`);
      if (audio) {
         audio.src = this.props.probdata[this.state.currentButton - 2].fields.learnaudio[0].url || null;
         audio.volume = this.state.audioVol;
         audio.play();
      }
      evt.stopPropagation();
   }
   setAudio() {
      let level = this.state.audioVol;
      if (level === 0) level = 1;
      else level = 0;
      this.setState({audioVol : level}, function() {
      let audio = document.getElementById(`${this.props.audio_abbrev}${this.state.currentButton - 1}`);
      if (audio) audio.volume = level;
      });
   }
   testMode() {
      this.setState({currentButton : 2, mode : 'test', currentAnswerMark : 1});
      setTimeout(function(){window.MathJax.Hub.Queue(['Typeset', window.MathJax.Hub]);}, 50);
      const stepWindow = document.getElementById('steps-container');
      stepWindow.scroll({top: 0, behavior: 'smooth' });
      let sound = document.getElementById(`${this.props.audio_abbrev}${this.state.currentButton - 1}`);
      if (sound) {sound.pause(); sound.currentTime = 0;}
   }
   updateTest() {
      $('.choice-button').attr('disabled', false);
      $('.choice-button').css('background-color', 'initial');
      let testnumber = this.state.testKey + 1;
      this.setState({currentButton : 2, mode : 'test', currentAnswerMark : 1, testKey : testnumber}, function() {
      let ding = document.getElementById('quizshow');
      if (this.state.testKey % this.state.PNUM === 0) {
         ding.volume = 0.09;
         ding.play();
      }
      else {ding.pause(); ding.currentTime = 0;}
      });
      setTimeout(function(){window.MathJax.Hub.Queue(['Typeset', window.MathJax.Hub]);}, 50);
      const stepWindow = document.getElementById('steps-container');
      stepWindow.scroll({top: 0, behavior: 'smooth' });
   }
   learnMode() {
      this.setState({currentButton : 1, mode : 'learn', currentAnswerMark : 1});
      setTimeout(function(){window.MathJax.Hub.Queue(['Typeset', window.MathJax.Hub]);}, 50);
      const stepWindow = document.getElementById('steps-container');
      stepWindow.scroll({top: 0, behavior: 'smooth' });
   }
   updateState(num, ans) {
      this.setState({testKey : num, currentAnswerMark : ans, mode : 'test'});
      setTimeout(function(){window.MathJax.Hub.Queue(['Typeset', window.MathJax.Hub]);}, 50);
      if (ans <= this.state.PNUM) {
      const cardLoc = document.getElementById(`card${ans}`).offsetTop;
      const stepWindow = document.getElementById('steps-container');
      const stepWindowLoc = document.getElementById('steps-container').offsetTop;
      setTimeout(function(){
      stepWindow.scroll({top: cardLoc - stepWindowLoc, behavior: 'smooth' });
      }, 800);
      }
   }
   render() {
      if (!this.props.probdata) return null;
      let modeMedia, butStyle, buttonChoices, mediaStyle, testText, reflect, audvis, testcode, tst, steps;
      let multChoiceButs = [];
      let stepCards = [];
      mediaStyle = this.state.mediaDims;

      if (this.state.currentButton <= this.props.probdata.length - 2) butStyle = {pointerEvents : 'none', backgroundColor : this.props.discolor};
      else butStyle = {pointerEvents : 'all', backgroundColor : 'white'};

      if (this.state.mode === 'learn') {
      $('#media-container').css({'background' : 'radial-gradient(white, #fafafa)'});
      for (let a = 0; a < this.props.probdata.length - 2; a++) {
         stepCards.push(<StepCard key={this.props.probdata[a].fields.id} label={this.props.probdata[a].fields.label} text={this.props.probdata[a].fields.text} order={this.props.probdata[a].fields.order} handler={this.childClick} curbut={this.state.currentButton} curmode={this.state.mode} upone={this.upOne} repstep={this.repeatStep} primcolor={this.props.primcolor} seccolor={this.props.seccolor} discolor={this.props.discolor} dislabel={this.props.dislabel} margbot={a === this.props.probdata.length - 3 ? 'uk-margin-large-bottom' : 'uk-margin-small-bottom'} clickData={this.props.clickData} abbrev={this.props.audio_abbrev.slice(0, this.props.audio_abbrev.indexOf('_'))} learnpointsper = {this.state.learnPointsPer} />)
      }

      buttonChoices = (
         <span>
         <button onClick={this.startOver} className='uk-button uk-button-default uk-button-small uk-width-1-3' style={butStyle}>redo</button>

         <button style={{backgroundColor : this.props.seccolor}} onClick={this.reset} className='uk-button uk-button-secondary uk-button-small uk-width-1-3'>clear</button>

         <button style={{backgroundColor : this.props.primcolor}} onClick={this.testMode} className='uk-button uk-button-primary uk-button-small uk-width-1-3'>show</button>
         </span>
      );
      let attachNum = 0;
      if (this.state.mediaDimTag === '_square') attachNum = 1;
      let src;
      if (this.state.currentButton === 1) src = `sbs_aslf_coverimg${this.state.mediaDimTag}.svg`;
      else src = this.props.probdata[this.state.currentButton - 2].fields.learnimg[attachNum].url;
      modeMedia = <img src={src} alt='' />;
      if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
         audvis = 'hidden';
      }
      else audvis = 'visible';
      reflect = <Reflect suganswer={this.props.probdata[this.props.probdata.length - 1].fields.text} reflectQ={this.props.probdata[this.props.probdata.length - 2].fields.text} />;
      }
      else  {
      $('#media-container').css({
      'background-image': 'radial-gradient(#e3e3e3 20%, transparent 20%), radial-gradient(#fafafa 20%, transparent 20%)',
      'background-color': 'white', 'background-position': '0 0, 50px 50px', 'background-size': '10px 10px'
      });
      if (this.state.currentButton < this.props.probdata.length - 2) butStyle = {pointerEvents : 'none', backgroundColor : this.props.discolor};
      else butStyle = {pointerEvents : 'all', backgroundColor : 'white'};

      if (this.state.testKey % this.state.PNUM === 1 || this.state.PNUM === 1) testcode = 'test';
      else if (this.state.testKey % this.state.PNUM === 0) testcode = `test${this.state.PNUM}`;
      else testcode = `test${this.state.testKey % this.state.PNUM}`;

      steps = 0;
      for (var q = 0; q < this.props.probdata.length - 2; q++) {
         if (this.props.probdata[q].fields[testcode] !== undefined) steps += 1;
      }

      buttonChoices = (
         <span>
         <button id='goToLearn' style={{backgroundColor : this.props.seccolor}} onClick={this.learnMode} className='uk-button uk-button-primary uk-button-small uk-width-1-2'>learn&thinsp;<span uk-icon='social'></span></button>

         <button id='updateTest' style={{backgroundColor : this.props.primcolor}} onClick={this.updateTest} className='uk-button uk-button-secondary uk-button-small uk-width-1-2'>{this.state.testKey % this.state.PNUM !== 0 ? 'next' : 'again'}</button>
         </span>
      );

      for (let a = 0; a < steps; a++) {
         multChoiceButs = [];
         if (this.state.testKey % this.state.PNUM === 1 || this.state.PNUM === 1) testText = this.props.probdata[a].fields[testcode];
         else if (this.state.testKey % this.state.PNUM === 0) testText = this.props.probdata[a].fields[`test${this.state.PNUM}`];
         else testText = this.props.probdata[a].fields[`test${this.state.testKey % this.state.PNUM}`];

        multChoiceButs.push(
        <div key={`${testcode}${a}`}>
        {this.props.probdata[a].fields[`${testcode}butenabled`].indexOf('A') !== -1 ? <button id={`${testcode}${a}0`} className='choice-button choiceA' style={this.props.probdata[a].fields.order > this.state.currentAnswerMark ? {display : 'none'} : {display : 'flex'}}><span style={{color : '#33ccff'}}>Ⓐ</span>&nbsp;&nbsp; {this.props.probdata[a].fields[`${testcode}choiceA`]}</button> : null}
        {this.props.probdata[a].fields[`${testcode}butenabled`].indexOf('B') !== -1 ? <button id={`${testcode}${a}1`} className='choice-button choiceB' style={this.props.probdata[a].fields.order > this.state.currentAnswerMark ? {display : 'none'} : {display : 'flex'}}><span style={{color : '#8a8ab1'}}>Ⓑ</span>&nbsp;&nbsp; {this.props.probdata[a].fields[`${testcode}choiceB`]}</button> : null}
        {this.props.probdata[a].fields[`${testcode}butenabled`].indexOf('C') !== -1 ? <button id={`${testcode}${a}2`} className='choice-button choiceC' style={this.props.probdata[a].fields.order > this.state.currentAnswerMark ? {display : 'none'} : {display : 'flex'}}><span style={{color : '#33cc33'}}>Ⓒ</span>&nbsp;&nbsp; {this.props.probdata[a].fields[`${testcode}choiceC`]}</button> : null}
        {this.props.probdata[a].fields[`${testcode}butenabled`].indexOf('D') !== -1 ? <button id={`${testcode}${a}3`} className='choice-button choiceD' style={this.props.probdata[a].fields.order > this.state.currentAnswerMark ? {display : 'none'} : {display : 'flex'}}><span style={{color : '#cc99cc'}}>Ⓓ</span>&nbsp;&nbsp; {this.props.probdata[a].fields[`${testcode}choiceD`]}</button> : null}
        </div>
        );

         stepCards.push(<StepCard key={this.props.probdata[a].fields.id} label={this.props.probdata[a].fields.testLabel} text={testText} order={this.props.probdata[a].fields.order} handler={this.childClick} curbut={this.state.currentButton} curmode={this.state.mode} curmark={this.state.currentAnswerMark} primcolor={this.props.primcolor} seccolor={this.props.seccolor} discolor={this.props.discolor} dislabel={this.props.dislabel} margbot='uk-margin-small-bottom' multbuts={multChoiceButs} probdata={this.props.probdata} curans={this.state.currentAnswerMark} clickData={this.props.clickData} abbrev={this.props.audio_abbrev.slice(0, this.props.audio_abbrev.indexOf('_'))} testnum={this.state.testKey} testpointsper={this.state.testPointsPer} pnum={this.state.PNUM} />)
      }

      modeMedia = <InteractiveSVG testnum={this.state.testKey} curbut={this.state.currentButton} correctans={this.correctAnswer} curans={this.state.currentAnswerMark} tested={this.userTested} pnum={this.state.PNUM} updatestate = {this.updateState} probdata={this.props.probdata} mediaDimTag={this.state.mediaDimTag} curmode={this.state.mode} testcode={testcode} steps={steps} incorrectans={this.incorrectAnswer} />;

      reflect = null;
      audvis = 'hidden';

      if (this.state.PNUM === 1) tst = this.state.currentAnswerMark;
      else if (testcode === 'test') tst = '1';
      else tst = testcode.slice(4);

      if (this.state.PNUM === 1 && this.state.currentAnswerMark > this.props.probdata.length - 2) tst = this.props.probdata.length - 2;
      }
      return (
      <div>
      <div id='media-container' className='uk-container uk-width-1-2@m uk-border-rounded' style={mediaStyle}>
      {modeMedia}
      <img onClick={this.setAudio} id='soundicon' src='audio_icon.svg' alt='' style={{opacity : ((1 - this.state.audioVol) * 1.5) / 1.5 + 0.2, visibility : audvis}} />
      {this.state.mode === 'test' ? <div id='testNumber'>{tst}</div> : null}
      </div>
      <div id='steps-container' className='uk-container uk-width-1-2@m'>
      {stepCards}
      {reflect}
      {buttonChoices}
      </div>
      </div>
      );
   }
}

export default ImageInteractivePractice;
