import React, {Component} from 'react';

class Scorer extends Component {
   constructor() {
      super();
      this.state = {};
   }
   render() {
   let displayClass, currentClickData, prefix, displayPoints;

   currentClickData = this.props.clickData;

   if (this.props.curmode === 'learn') {
   let clicked = this.props.clickData.learn[`step${this.props.order}`];
   if (clicked) {displayClass = ''; prefix = ''; displayPoints = this.props.clickData.learn[`step${this.props.order}`];}
   else if (this.props.curbut - 1 === this.props.order) {
      displayClass = 'uk-animation-slide-right-small';
      displayPoints = this.props.points;
      currentClickData.learn[`step${this.props.order}`] = this.props.points;
      currentClickData.score += this.props.points;
      prefix = '+';
      let ding;
      if (this.props.curbut - 1 === 1) ding = document.getElementById('doneprob1');
      else if (this.props.curbut - 1 === 2) ding = document.getElementById('doneprob3');
      else if (this.props.curbut - 1 === 3) ding = document.getElementById('doneprob4');
      else if (this.props.curbut - 1 === 4) ding = document.getElementById('doneprob5');
      else if (this.props.curbut - 1 >= 5) ding = document.getElementById('doneprob6');
      ding.volume = 0.1;
      ding.play();
   }
   else {displayClass = 'rewardHidden'; prefix=''; displayPoints = '';}
   }

   else if (this.props.curmode === 'test' && this.props.testnum % this.props.pnum !== 0) {
   let tnum = this.props.testnum % this.props.pnum;

   let clicked = this.props.clickData[`test${tnum}`][`step${this.props.order}`];
   if (clicked) {displayClass = 'uk-animation-slide-right-small'; prefix = ''; displayPoints = this.props.clickData[`test${this.props.testnum}`][`step${this.props.order}`];}
   else if (this.props.curans - 1 === this.props.order) {
      displayClass = 'uk-animation-slide-right-small';
      displayPoints = this.props.points;
      currentClickData[`test${tnum}`][`step${this.props.order}`] = this.props.points;
      currentClickData.score += this.props.points;
      prefix = '+';
      let ding;
      if (this.props.curans - 1 === 1) ding = document.getElementById('doneprob1');
      else if (this.props.curans - 1 === 2) ding = document.getElementById('doneprob3');
      else if (this.props.curans - 1 === 3) ding = document.getElementById('doneprob4');
      else if (this.props.curans - 1 === 4) ding = document.getElementById('doneprob5');
      else if (this.props.curans - 1 >= 5) ding = document.getElementById('doneprob6');
      ding.volume = 0.1;
      ding.play();
   }
   else {displayClass = 'rewardHidden'; prefix = ''; displayPoints = '';}
   }

   else if (this.props.curmode === 'test' && this.props.testnum % this.props.pnum === 0) {
      displayClass = 'rewardHidden';
      prefix = '';
      displayPoints = '';
      if (this.props.curans - 1 === this.props.order) {
        let ding;
        if (this.props.curans - 1 === 1) ding = document.getElementById('doneprob1');
        else if (this.props.curans - 1 === 2) ding = document.getElementById('doneprob3');
        else if (this.props.curans - 1 === 3) ding = document.getElementById('doneprob4');
        else if (this.props.curans - 1 === 4) ding = document.getElementById('doneprob5');
        else if (this.props.curans - 1 >= 5) ding = document.getElementById('doneprob6');
        ding.volume = 0.1;
        ding.play();
      }
   }

   localStorage.setItem(this.props.abbrev, JSON.stringify(currentClickData));

   return (
      <>
      <div id='scorewheel'></div>
      <span className={this.props.curmode === 'learn' ? `rewardLearn ${displayClass}` : `rewardTest ${displayClass}`}>
      {`${prefix}${displayPoints}`}
      </span>
      </>
   );
   }
}

export default Scorer;
