import React, {Component} from 'react';
import ImageInteractivePractice from './ImageInteractivePractice';
import smoothscroll from 'smoothscroll-polyfill';

let prob_data = null;
let testCount = null;
let audio_abbrev = 'aslf_narration';
let clickData;

class App extends Component {
   constructor() {
      super();
      this.state = {data : null, PNUM : null};
   }
   componentDidMount() {
      fetch('https://api.airtable.com/v0/appbJvIOkBMSrEz8z/Adding%20Like%20Fractions?api_key=keyuFtFTBZMHrzjOn')
      .then((resp) => resp.json())
      .then(data => {
      prob_data = data.records.sort(function(a, b){return a.fields.order - b.fields.order});
      testCount = 0;
      for (var a = 0; a < (Object.keys(prob_data[0].fields)).length; a++) {
      if ((Object.keys(prob_data[0].fields))[a].match(/^test\d*$/)) testCount += 1;
      }
      this.setState({data : prob_data, PNUM : testCount});
      });
      window.onbeforeunload = function() {window.speechSynthesis.cancel();};
      smoothscroll.polyfill();
      if (localStorage.getItem('aslf') === null) {
      let aslf = {score : 0, banked_score : 0, lastTestPointsPer : 50, learn : {step1 : false, step2 : false, step3 : false, step4 : false, step5 : false}, test1 : {step1 : false, step2 : false, step3 : false, step4 : false, step5 : false}, test2 : {step1 : false, step2 : false, step3 : false, step4 : false, step5 : false}, test3 : {step1 : false, step2 : false, step3 : false, step4 : false, step5 : false}, test4 : {step1 : false, step2 : false, step3 : false}, test5 : {step1 : false, step2 : false, step3 : false}, test6 : {step1 : false, step2 : false}, test7 : {step1 : false, step2 : false, step3 : false, step4 : false, step5 : false}, test8 : {step1 : false, step2 : false, step3 : false, step4 : false, step5 : false}, test9 : {step1 : false, step2 : false, step3 : false, step4 : false, step5 : false}};
      localStorage.setItem('aslf', JSON.stringify(aslf));
      }
      clickData = JSON.parse(localStorage.getItem('aslf'));
   }
   render() {
      if (!prob_data && !testCount) return null;
      let primary = '#4499cc';
      let secondary = '#557722';
      const disabled = '#D3D3D3';
      let dislabel = '#aaccdd';

      let scala;
      if (this.props.media === 'images-interactive') {
         scala = <ImageInteractivePractice primcolor={primary} seccolor={secondary} discolor={disabled} dislabel={dislabel} suffix='aslf' probdata={this.state.data} pnum={this.state.PNUM} audio_abbrev={audio_abbrev} clickData={clickData} />
      }
      return (
      <div>
      {scala}
      </div>
      );
   }
}

export default App;
