import React, {Component} from 'react';
import $ from 'jquery';

var socket = null;
var userID = null;
var usernname = null;
var joinInterval;
var banInterval;
var stateInterval;
var voices;
var oldAns = 100;

class InteractiveSVG extends Component {
   constructor() {
      super();
      this.state = {SOCKETON : false, numusers : 0, is_teacher : false, users : {}, nnames : {}, curAssigned : false, teacher_exists : false, talkVol : 0};
      this.handleAnswers = this.handleAnswers.bind(this);
      this.clearAnswer = this.clearAnswer.bind(this);
      this.startSocket = this.startSocket.bind(this);
      this.receiveUser = this.receiveUser.bind(this);
      this.endUser = this.endUser.bind(this);
      this.sendAssign = this.sendAssign.bind(this);
      this.receiveAssign = this.receiveAssign.bind(this);
      this.handleMathAnswer = this.handleMathAnswer.bind(this);
      this.setTalkVol = this.setTalkVol.bind(this);
      this.handleChoiceClick = this.handleChoiceClick.bind(this);
   }
  handleMathAnswer(choice) {
     window.speechSynthesis.cancel();
     if (this.state.SOCKETON && !this.state.is_teacher) {
     if (($(`.choice${choice}`).attr('class')).indexOf('hvr-bob') === -1) {
        $(`.choice${choice}`).attr('class', `choice${choice} choice-button hvr-bob`);
        ($('.choice-button').not(`.choice${choice}`)).each(function(inx) {$(this).removeClass('hvr-bob');});
     }
     else {
        $(`.choice${choice}`).attr('class', `choice${choice} choice-button`)
        choice = null;
     }
     }
     this.handleAnswers(choice);
  }
  handleChoiceClick(evt) {
     if ($(evt.target).hasClass('choiceA')) {this.handleMathAnswer('A');}
     else if ($(evt.target).hasClass('choiceB')) {this.handleMathAnswer('B');}
     else if ($(evt.target).hasClass('choiceC')) {this.handleMathAnswer('C');}
     else if ($(evt.target).hasClass('choiceD')) {this.handleMathAnswer('D');}
  }
  componentDidMount() {
    window.addEventListener('touchmove', function() {});
    window.addEventListener('beforeunload', function() {socket.send(JSON.stringify({action : 'userend', userID : userID}));});
    window.addEventListener('click', this.handleChoiceClick);
    window.speechSynthesis.onvoiceschanged = function() {voices = window.speechSynthesis.getVoices();}
  }
  componentWillUnmount() {
     clearInterval(joinInterval);
     window.removeEventListener('click', this.handleChoiceClick);
  }
  handleMessage(event) {
     if (!this.state.SOCKETON) return;
     var message = JSON.parse(event.data);
     if (message.action === 'submit') this.handleReceiveAnswers(message.ans, message.user, message.tAns);
     else if (message.action === 'usercheck') this.receiveUser(message.userID, message.nname, message.tExist);
     else if (message.action === 'userend') this.endUser(message.userID);
     else if (message.action === 'assign') this.receiveAssign(message.userID);
     else if (message.action === 'unassign') this.receiveUnassign();
     else if (message.action === 'emit-state') this.props.updatestate(message.tnum, message.cans);
  }
  startSocket(evt) {
     let roomID = $('#socketID').val();
     if ($('#teacherchck').prop("checked") === true) roomID += 'T$';
     let teacher_exists = 9999999999999;
     this.setState({teacher_exists : teacher_exists});
     if (roomID.match(/[A-Za-z0-9_-]+[T$]$/g) !== null) {
        teacher_exists = new Date().getTime();
        roomID = roomID.slice(0, -2);
        $('#socketID').val(roomID);
        this.setState({is_teacher : true, teacher_exists : teacher_exists});
        setTimeout(function(){
        if (this.state.is_teacher) {
        stateInterval = setInterval(function() {
        socket.send(JSON.stringify({action : 'emit-state', tnum : this.props.testnum, cans : this.props.curans}));
        }.bind(this), 1580);
        }
        else {
        clearInterval(stateInterval);
        }
        }.bind(this), 3000);
     }
     else $('#updateTest').css({'pointer-events' : 'none'});
     if (roomID === '' || roomID.match(/[^A-Za-z0-9_-]+/g) !== null) return;
     usernname = $('#nickname').val();
     if (usernname === '') usernname = '*';
     socket = new WebSocket("wss://connect.websocket.in/addlikefracs?room_id=" + roomID);
     socket.addEventListener('message', this.handleMessage.bind(this));
     this.setState({SOCKETON : true});
     window.UIkit.modal(document.getElementById('modal-example')).hide();
     userID = String(Math.floor(Math.random() * 1000000));
     $('#goToLearn').css({'pointer-events' : 'none'});
     joinInterval = setInterval(function(){
     socket.send(JSON.stringify({action : 'usercheck', userID : userID, nname : usernname, tExist : teacher_exists}));
     }, 2000);
  }
  receiveUser(msg, name, tExist) {
     for (var prop in this.state.users) if (this.state.users[prop] === msg) return;
     let newUsers = this.state.users;
     newUsers[String(this.state.numusers + 1)] = msg;
     let newNames = this.state.nnames;
     newNames[String(this.state.numusers + 1)] = name;
     this.setState({numusers : this.state.numusers + 1, users : newUsers, nnames : newNames});

     if (tExist < this.state.teacher_exists) {
        this.setState({is_teacher : false, teacher_exists : true});
     }
  }
  endUser(msg) {
    if (this.state.curAssigned === msg) socket.send(JSON.stringify({action : 'unassign'}));
    $('#' + msg).css({'pointer-events' : 'none', 'visibility' : 'hidden'});
    $('#' + msg).prop('disabled', true);
  }
  handleAnswers(ans) {
     let anscode;
     let ansobj = {'A' : 0, 'B' : 1, 'C' : 2, 'D' : 3};
     $('.choice-button').removeClass('wrong-answer');
     if (this.props.testnum % this.props.pnum === 0) anscode = this.props.pnum;
     else anscode = this.props.testnum % this.props.pnum;
     let correctAnswer = this.props.probdata[this.props.curans - 1].fields[`testans${anscode}`];

     if (this.state.SOCKETON) {
     socket.send(JSON.stringify({action : 'submit', ans : ans, user : userID, tAns : this.state.is_teacher}));
     }
     if (ans === correctAnswer && ((!this.state.SOCKETON) || (this.state.SOCKETON && this.state.is_teacher))) {
     this.props.correctans(this.props.curans < this.props.steps ? false : true);
     }
     else if (ans !== correctAnswer && ((!this.state.SOCKETON) || (this.state.SOCKETON && this.state.is_teacher))) {
     $(`#${this.props.testcode}${this.props.curans - 1}${ansobj[ans]}`).addClass('wrong-answer');
     this.props.incorrectans();
     }
  }
  handleReceiveAnswers(ans, id, tans) {
     if (this.state.is_teacher) {
     var choice_colors = {'a' : 'rgb(150, 226, 252)', 'b' : 'rgb(178, 178, 204)',
                          'c' : 'rgb(153, 229, 153)', 'd' : 'rgb(228, 203, 228)'};
     if (!ans) $(`#${id}`).css({'background-color' : '#ffffff'});
     else $(`#${id}`).css({'background-color' : choice_colors[ans.toLowerCase()]});
     }
     else if (tans) {
     let anscode;
     if (this.props.testnum % this.props.pnum === 0) anscode = this.props.pnum;
     else anscode = this.props.testnum % this.props.pnum;
     let correctAnswer = this.props.probdata[this.props.curans - 1].fields[`testans${anscode}`];
     if (ans === correctAnswer) {
     this.props.correctans(this.props.curans < this.props.probdata.length - 2 ? false : true);
     $('.choice-button').removeClass('hvr-bob');
     }
     }
  }
  clearAnswer() {
     this.editor.clear();
     $('#answerInput').css({'color' : '#fafafa'});
     setTimeout(function(){$('#answerInput').css({'color' : 'black'});}, 1000);
  }
  sendAssign(evt) {
     let blockAll = this.state.curAssigned === 'teacher';
     if (evt.currentTarget.id === 'ban-button' && this.state.is_teacher && !blockAll) {
        $('#ban-button').css({'background-color' : 'rgba(255, 0, 0, 0.8)'});
        $('.userbutton').css({'background-color' : '#ffffff'});
        this.setState({curAssigned : 'teacher'});
        banInterval = setInterval(function(){
           socket.send(JSON.stringify({action : 'assign', userID : 'teacher'}));
        }, 2000);
        return;
     }
     else if (evt.currentTarget.id === 'ban-button' && this.state.is_teacher && blockAll) {
        $('#ban-button').css({'background-color' : 'lightblue'});
        socket.send(JSON.stringify({action : 'unassign'}));
        this.setState({curAssigned : false});
        clearInterval(banInterval);
        return;
     }
     else if ($('#' + evt.currentTarget.id).css('background-color') !== 'rgb(144, 238, 144)') {
        clearInterval(banInterval);
        $('#ban-button').css({'background-color' : 'lightblue'});
        $('#' + evt.currentTarget.id).css({'background-color' : 'rgb(235, 233, 141)'});
        $('.userbutton').not('#' + evt.currentTarget.id).css({'background-color' : '#ffffff'});
        socket.send(JSON.stringify({action : 'assign', userID : evt.currentTarget.id}));
        this.setState({curAssigned : evt.currentTarget.id});
     }
     else {
        $('#ban-button').css({'background-color' : 'lightblue'});
        $('#' + evt.currentTarget.id).css({'background-color' : '#ffffff'});
        socket.send(JSON.stringify({action : 'unassign'}));
        this.setState({curAssigned : false});
     }
  }
  receiveAssign(id) {
     if (userID === id && this.state.curAssigned === false) {
        $('#coverCanvas').css({'display' : 'none'});
        $('#assignModal').trigger('click');
        this.setState({curAssigned : true});
     }
     else if (userID === id && this.state.curAssigned === true) {
        this.setState({curAssigned : false});
     }
     else if (userID !== id) {
        $('#coverCanvas').css({'display' : 'inline'});
        this.setState({curAssigned : false});
     }
  }
  receiveUnassign() {
     $('#coverCanvas').css({'display' : 'none'});
     this.setState({curAssigned : false});
  }
  setTalkVol() {
     if (this.state.talkVol === 1) {window.speechSynthesis.cancel(); this.setState({talkVol : 0});}
     else {this.setState({talkVol : 1});}
  }
  render() {
    let socketStyle, multChoiceButs;
    let userbuttons = [];

    if (this.state.SOCKETON) socketStyle = {backgroundColor : '#00ff00'};
    else socketStyle = {backgroundColor : '#D3D3D3'};
    if (this.state.is_teacher) socketStyle = {backgroundColor : '#ff00ff'};

    let userlist = Object.keys(this.state.users);
    let namelist = Object.keys(this.state.nnames);
    for (var a = 0; a < userlist.length; a++) {
    let toppos = (a + 1) * 35 + 5;
    userbuttons.push(
      <button className='userbutton uk-animation-slide-left' key={a} id={this.state.users[userlist[a]]} uk-tooltip={`title: ${this.state.nnames[namelist[a]]}; pos: right`} style={{top : toppos}}>
      <span uk-icon="happy"></span>
      </button>
    );
    }

    if (this.props.curans <= this.props.probdata.length - 2 && !this.state.SOCKETON && this.state.talkVol === 1 && this.props.curans !== oldAns) {
    var synth = window.speechSynthesis;
    var utter = new SpeechSynthesisUtterance(this.props.probdata[this.props.curans - 1].fields[`${this.props.testcode}readable`]);
    utter.volume = this.state.talkVol;
    setTimeout(function(){utter.voice = voices[5]; synth.speak(utter);}, 1000);
    oldAns = this.props.curans;
    }

    let attachNum = 0;
    if (this.props.mediaDimTag === '_square') attachNum = 1;
    let testcode
    if (this.props.testnum % this.props.pnum === 1 || this.props.pnum === 1) testcode = 'testimg';
    else if (this.props.testnum % this.props.pnum === 0) testcode = `testimg${this.props.pnum}`;
    else testcode = `testimg${this.props.testnum % this.props.pnum}`;

    let srccode = this.props.curans - 1;
    if (this.props.curans >= this.props.steps) srccode = this.props.steps - 1;
    let src = this.props.probdata[srccode].fields[testcode][attachNum].url;
    return (
    <div id='interactive'>
    <img id='curTestImg' src={src} alt='' style={{height : '90%'}} />
    <img onClick={this.setTalkVol} id='talkicon' src='audio_icon.svg' alt='' style={{opacity : ((1 - this.state.talkVol) * 1.5) / 1.5 + 0.2}} />
    {this.props.curmode === 'test' ? multChoiceButs : null}
    <button id='openModal' type="button" uk-toggle="target: #modal-example" style={socketStyle}><span uk-icon="more"></span></button>
    <button id='assignModal' type="button" uk-toggle="target: #modal-assign" style={{display : 'none'}}></button>

    <div id="modal-example" uk-modal='true'>
    <div className="uk-modal-dialog uk-modal-body uk-overflow-auto">
    <button className="uk-modal-close-default" type="button" uk-close='true'></button>
    <h4 className="uk-modal-title">Guided Practice</h4>
    <p className='uk-text-justify'>Enter a Room ID (e.g., mrscala_203). Then click to join an interactive practice session. If you are leading, share the ID for others to join. If you are joining, enter the ID shared with you and enter a name if you want to.</p>
    <p className="uk-text-right"><input id='socketID' placeholder='Room ID'></input></p>
    <p className="uk-text-right"><input id='nickname' placeholder='Name | Nickname' maxLength='10'></input></p>
    <p className='uk-text-justify'>Entering numbers, letters, hyphens, and underscore characters is okay, up to a maximum of 20 characters.</p>
    <p className="uk-text-right">
    <button className="uk-button uk-button-primary" type="button" onClick={this.startSocket}>Join Session</button><br />
    <input type='checkbox' className='uk-checkbox' name='teachercheck' id='teacherchck' />
    <label htmlFor='teachercheck'>&nbsp;&nbsp;Teacher</label>
    </p>
    </div>
    </div>

    {this.state.is_teacher ? <div id='userSideBar' style={{position : 'fixed', top : 0, left : 0, width : 150, height : '100vh', backgroundColor : 'navy'}}>
    {this.state.is_teacher ? userbuttons : null}
    {this.state.is_teacher ? <button className='banbutton' id='ban-button' style={{top : 0, left : 0}} onClick={this.sendAssign}><span uk-icon="ban"></span></button> : null}
    </div> : null}
    <div id='coverCanvas'></div>
    </div>
    );
  }
}

export default InteractiveSVG;
